module.exports = "## 文档解释\n\n如果熟悉 TypeScript，就可以很容易理解文档中的这些部分:\n\n* Props\n* defaultProps\n* Events\n\n下面举例说明。\n\n## Props\n\n> 所有属性、类型和可选值\n\n```tsx\n{\n  //控件的尺寸\n  size?: 'medium' | 'small' | 'mini';\n  //字体的颜色\n  color: string;\n  plain?: boolean;\n}\n```\n\n* `:` 前面的属性名，`:` 后面是类型或者可选值\n* `|` 分隔代表了可选的默认值\n* 注释代表属性的描述说明\n* 带有`?`的代表非必填\n\n## defaultProps \n\n> 代表属性的默认值\n\n```tsx\n{\n  size: 'medium' \n}\n```\n\n不是所有属性都需要有默认值。\n\n## Events\n\n> 组件支持的事件，属于 `CustomEvent`，所以可以从 `event.detail` 获取组件传递给事件监听器的数据。\n\n* change - 文本改变触发，event.detail 类型声明:\n```tsx\n{\n  value: string\n}\n```\n"

